import React from 'react'

export class AddPictureScreen extends React.Component {
  constructor(props) {
    super(props);
    this.render = this.render.bind(this)
  }

  render() {
    console.log(this.props)
    return (

      <div className="add-picture-screen">
        <button type="button" className="add-picture-screen__close-button close-button"
                onClick={this.props.onClose}/>
        <div className="add-picture-screen__request-info">
          <button className="add-picture-screen__edit-button"/>
          <div className="add-picture-screen__request-info-row">
            <p className="add-picture-screen__title">Адрес</p>
            <p className="add-picture-screen__subtitle">{this.props.region}</p>
          </div>
          <div className="add-picture-screen__request-info-row">
            <p className="add-picture-screen__title">Категория</p>
            <p className="add-picture-screen__subtitle">{this.props.category}</p>
          </div>
        </div>
        <button className="add-picture-screen__button">Добавить фото</button>
        <button className="add-picture-screen__button">Продолжить без фото</button>
      </div>
    )
  }
}